import dayjs from 'dayjs';
import React from "react";
import{ 
    Line, 
    XAxis,
    LineChart,
    ResponsiveContainer,
    ReferenceLine,
    CartesianGrid
} from 'recharts';
import { calculateBiorhythmSeries } from '../calculations';
import './BiorythmChart.css';

function formatDate(isoString) {
    return dayjs(isoString).format('D MMM');    
}

function BiorythmChart({ birthDate, targetDate }) {
    const startDate = dayjs(targetDate).subtract(15, 'days').toISOString();
    const data = calculateBiorhythmSeries(birthDate, startDate, 31)
    .map( (item)=>({ ...item, date: formatDate(item.date) }));
    return(
        <ResponsiveContainer className="biorythm-chart" width="100%" height={200}>
            <LineChart data={data}>

                <XAxis dataKey='date' ticks={[ data[3].date , data[15].date, data[27].date ]} />
                <CartesianGrid vertical={false} strokeDasharray="3 3" />
                <ReferenceLine x={data[15].date} />
                <Line dataKey='physical' type="natural" dot={false} className="physical" />
                <Line dataKey='emotional' type="natural" dot={false} className="emotional" />
                <Line dataKey='intellectual' type="natural" dot={false} className="intellectual" />

            </LineChart>
        </ResponsiveContainer>
    )
}

export default BiorythmChart;